const TYPE_LIST = ['attention', 'memory', 'solution', 'executive'];
const TYPE_MAP = {
    attention: '',
    memory: '',
    solution: '',
    executive: '',
};

const LEVEL_MAP = {
    attention: [60, 120],
    memory: [60, 140],
    solution: [60, 140],
    executive: [60, 120],
    total: [240, 520],
};

const TIP_MAP = {
    attention: '',
    memory: ', ',
    solution: '',
    executive: '',
};

const ASSESS_MAP_LIST = {
    attention: [
        ' ',
        ' ',
        '',
    ],
    memory: [
        ', ',
        ', ',
        ', ',
    ],
    solution: [
        '',
        '',
        '',
    ],
    executive: [
        '',
        '',
        ' ',
    ],
    total: [
        '',
        '',
        '',
    ],
};
// 
const numMap = ['', '', '', '', ''];

const getTextHead = (types) => {
    const { length } = types;
    return `${length === 1 ? '' : ''}${types.join('').replace(/([^]+?$)/g, '$1')}${numMap[length]}`;
};

const getAssess = (examData) => {
    const result = {};
    const count = {
        1: [],
        2: [],
        3: [],
    };
    TYPE_LIST.forEach((type) => {
        result[type] = {};
        const level = examData[`${type}_level`];
        const assessList = ASSESS_MAP_LIST[type];
        result[type].level = level;
        result[type].assess = assessList[level - 1];
        result[type].score = examData[`${type}_score`];
        result[type].over = examData[`${type}_over_percent`];
        count[level].push(TYPE_MAP[type]);
    });
    result.total = {};
    result.total.level = examData.total_level;
    result.total.score = examData.total_score;
    result.total.assess = ASSESS_MAP_LIST.total[examData.total_level - 1];
    result.total.over = examData.total_over_percent;
    if (count['3'].length > 0) {
        result.total.assess += `${getTextHead(count['3'])}`;
    }
    if (count['2'].length > 0) {
        result.total.assess += `${getTextHead(count['2'])}`;
    }
    if (count['1'].length > 0) {
        result.total.assess += `${getTextHead(count['1'])}`;
    }
    if (count['3'].length < 4) {
        result.total.assess += '';
    } else {
        result.total.assess += '';
    }
    return result;
};

const getScore = (data) => {
    const result = {};
    let totalScore = 0;
    TYPE_LIST.forEach((type) => {
        const { score } = data[type];
        const level = LEVEL_MAP[type];
        totalScore += score;
        result[`${type}_score`] = score;
        switch (true) {
            case score < level[0]: result[`${type}_level`] = '1'; break;
            case score >= level[0] && score < level[1]: result[`${type}_level`] = '2'; break;
            default: result[`${type}_level`] = '3'; break;
        }
    });
    switch (true) {
        case totalScore < LEVEL_MAP.total[0]: result.total_level = '1'; break;
        case totalScore >= LEVEL_MAP.total[0] && totalScore < LEVEL_MAP.total[1]:
            result.total_level = '2'; break;
        default: result.total_level = '3'; break;
    }
    result.total_score = totalScore;
    return result;
};


export {
    TYPE_LIST,
    TYPE_MAP,
    TIP_MAP,
    getAssess,
    getScore,
};



